<template>
    <div class="cart-item">
            <ul class="list-group">
              <li class="list-group-item active">
                <span><b>Name</b></span>
                <span class="float-right"><b>Price</b></span>
              </li>
              <li class="list-group-item" v-for="(item, index) in items" :key="index">
                <button class="btn btn-danger btn-xs" @click="removeItem(index)">X</button>
                <span>{{item.title}}</span>
                <span class="float-right">{{item.price}}</span>
              </li>
              <li class="list-group-item">
                <span><b>Total Price</b></span>
                <span class="float-right"><b>${{totalPrice}}</b></span>
              </li>
            </ul>
          </div>
</template>

<script>
export default {
  props: ['items'],
  computed:{
    totalPrice(){
      var total = 0;
      this.items.forEach(item => {
        total = total + parseFloat(item.price)
      });
      return total;
    }
  },
  methods:{
    removeItem(index){
      this.$emit("removeItem",index)
    }
  }
}
</script>

<style>

</style>



