<template>
  <div class="row">
    <div class="col-md-4 col-lg-4" v-for="(item,index) in items" :key="index">
        <div class="card">
        <img :src="item.photo" class="card-img-top" alt="">
        <div class="card-body">
            <h5 class="card-title">
            <span><a href="#">{{item.title}}</a></span>
            <span class="float-right">${{item.price}}</span>
            </h5>
            <p class="card-text">This is static descriptions dont worry we will uppdate descitouion as soon as possible</p>
            <a @click="addToCart(item)" class="btn btn-primary">Add To Cart</a>
        </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
    props:['items'],
    methods:{
    addToCart(item){
      this.$emit('newItemAdded',item)
    }
  }
}
</script>

<style>
    .card{
        margin-bottom: 20px;
    }
</style>



